import $ks from './ks';

class ImgPreloader {
    /*
    @ arg
    * preloadedImgArray(Array) : optional. 없을 경우 window의 객체로 임의의 Array 생성.
    */
    constructor(preloadedImgArray) {
        if (preloadedImgArray)
            this.preloadedImgArray = preloadedImgArray;
        else {
            window.preloadedImgArray = [];
            this.preloadedImgArray = window.preloadedImgArray;
        }
    }

    /*
    @ arg
    * opts = {
        array(Array) : optional. 이미지명을 배열로 직접 넣음. 이 옵션을 선택할 경우, 다른 옵션은 생략. 아래의 규칙 이용할 경우 false.
        path(String) : 규칙적인 이미지의 숫자를 제외한 앞의 경로.
        ext(String) : 이미지 확장자명
        count(Number) : 이미지 갯수
        startIndex(Number, default: 0) : 시작하는 이미지 번호
    }
    */
    preload(opts) {
        return new Promise((resolve, reject) => {
            let imgTag, imgSrc, ext, lessthan10 = false, lessthan100 = false, startIndex = opts.startIndex || 0;
            ext = opts.ext || 'png';

            if (opts.array) { // 배열을 넣었을 경우
                for (let i = 0, len = opts.array.length; i < len; i++) {
                    imgTag = new Image();
                    imgSrc = opts.array[i];
                    imgTag.src = imgSrc;
                    this.preloadedImgArray.push(imgTag);
                }
            } else { // 규칙을 넣었을 경우
                if (opts.count < 10) {
                    lessthan10 = true;
                } else if (opts.count < 100) {
                    lessthan10 = false;
                    lessthan100 = true;
                }

                for (let i = 0 + startIndex; i <= opts.count - 1 + startIndex; i++) {
                    if (lessthan10) {
                        imgSrc = `${opts.path}${i}.${ext}`;
                    } else if (lessthan100) {
                        imgSrc = i < 10 ? `${opts.path}0${i}.${ext}` : `${opts.path}${i}.${ext}`;
                    } else {
                        if (i < 10)
                            imgSrc = `${opts.path}00${i}.${ext}`;
                        else if (i < 100)
                            imgSrc = `${opts.path}0${i}.${ext}`;
                        else
                            imgSrc = `${opts.path}${i}.${ext}`;
                    }
                    imgTag = new Image();
                    imgTag.src = imgSrc;
                    this.preloadedImgArray.push(imgTag);
                }
            }

            let thisInterval = window.setInterval(() => {
                if (this.everyImgPreloaded) {
                    window.clearInterval(thisInterval);
                    window.setTimeout(resolve, 0);
                }
            }, 10);
        });
    }

    get everyImgPreloaded() { return this.preloadedImgArray.every((img) => { return img.complete; }); }
}

export default ImgPreloader;