import groupBgInfo from "./groupBgInfo";
import starScaleSet from "./starScaleSet";

let setLastGroupMarginRight = (group, i, groupCtn) => {
    let cond = (index) => { return groupCtn === index && i === index; },
        setMarginR = (px) => { group.style.marginRight = `${px}px` };

    if (cond(5) || cond(7) || cond(9) || cond(14) || cond(18))
        setMarginR(640);
    else if (cond(9, 9) || cond(11))
        setMarginR(428);
    else if (cond(13) || cond(17))
        setMarginR(960);
    else if (cond(15) || cond(19))
        setMarginR(320);
};

let setGroupSize = (group, setAll, groupCtn) => {
    groupBgInfo.forEach((obj) => {
        if (groupCtn <= obj.ltArrange) {
            group.style.width = `${obj.width}px`;
            group.style.height = `${obj.height}px`;
            group.style.background = `url(${obj.imgUrl})`;
            setAll.style.flexBasis = `${obj.setAllWidth}px`;
        }
    });
};

let setGroupPadding = (ul, groupCtn, starCtn) => {
    let checkGroup = (gt, lt) => { return groupCtn >= gt && groupCtn <= lt; }
    let checkStar = (gt, lt) => { return starCtn >= gt && starCtn <= lt; }
    let setPadding = (p1, p2) => { ul.style.padding = `${p1}px ${p2}px`; }

    if (checkGroup(7, 8)) {
        if (checkStar(15, 18)) setPadding(10, 0);
        else if (checkStar(19, 20)) setPadding(15, 0);
    } else if (checkGroup(9, 10)) {
        if (checkStar(13, 16)) setPadding(5, 0);
        else if (checkStar(17, 20)) setPadding(10, 0);
    } else if (checkGroup(13, 16)) {
        if (checkStar(5, 8)) setPadding(20, 0);
    } else if (checkGroup(17, 20)) {
        if (checkStar(5, 8)) setPadding(15, 0);
        if (checkStar(17, 20)) setPadding(0, 20);
    }
};

let setStarPosition = (star, groupCnt, starCtn) => {
    let starCntCond = (gt, lt) => {
        if (!lt) return starCtn === gt;
        else return starCtn >= gt && starCtn <= lt;
    }

    let setMargin = (m1, m2) => star.style.margin = `${m1}px ${m2}px`;

    if (groupCnt === 3) {
        if      (starCntCond(5))        setMargin(12, 14);
        else if (starCntCond(6))        setMargin(12, 1);
        else if (starCntCond(7, 8))     setMargin(-23, 32);
        else if (starCntCond(9, 10))    setMargin(-23, 14);
        else if (starCntCond(11, 12))   setMargin(-23, 1);
        else if (starCntCond(13, 14))   setMargin(-23, -8);
        else if (starCntCond(15, 16))   setMargin(-23, -14);
        else if (starCntCond(17, 18))   setMargin(-16, -13);
        else if (starCntCond(19, 20))   setMargin(-16, -17);
    } else if (groupCnt === 4) {
        if      (starCntCond(5))        setMargin(4, 4);
        else if (starCntCond(6))        setMargin(4, -7);
        else if (starCntCond(7, 8))     setMargin(-21, 26);
        else if (starCntCond(9, 10))    setMargin(-21, 10);
        else if (starCntCond(11, 12))   setMargin(-21, -1);
        else if (starCntCond(13, 14))   setMargin(-21, -8);
        else if (starCntCond(15, 16))   setMargin(-21, -14);
        else if (starCntCond(17, 18))   setMargin(-15, -12.2);
        else if (starCntCond(19, 20))   setMargin(-15, -15.8);
    } else if (groupCnt === 5 || groupCnt === 6) {
        if      (starCntCond(5, 8))     setMargin(-1, -2.2);
        else if (starCntCond(9, 12))    setMargin(-10, 3.8);
        else if (starCntCond(13, 16))   setMargin(-12, 9.8);
        else if (starCntCond(17, 20))   setMargin(-12, 0.7);
    } else if (groupCnt === 7 || groupCnt === 8) {
        if      (starCntCond(5))        setMargin(6, -8.9);
        else if (starCntCond(6))        setMargin(6, -16.4);
        else if (starCntCond(7, 8))     setMargin(-11, 14.5);
        else if (starCntCond(9, 10))    setMargin(-11, 3.2);
        else if (starCntCond(11, 12))   setMargin(-11, -4.4);
        else if (starCntCond(13, 14))   setMargin(-11, -9.8);
        else if (starCntCond(15, 16))   setMargin(-10, -7.9);
        else if (starCntCond(17, 18))   setMargin(-10, -10.9);
        else if (starCntCond(19, 20))   setMargin(-7, -7.5);
    } else if (groupCnt === 9 || groupCnt === 10) {
        if      (starCntCond(5))        setMargin(8, -7);
        else if (starCntCond(6))        setMargin(8, -13.8);
        else if (starCntCond(7, 8))     setMargin(-13, 9.3);
        else if (starCntCond(9, 10))    setMargin(-13, -1);
        else if (starCntCond(11, 12))   setMargin(-13, -7.8);
        else if (starCntCond(13, 14))   setMargin(-10, -6.7);
        else if (starCntCond(15, 16))   setMargin(-10, -10.4);
        else if (starCntCond(17, 18))   setMargin(-7, -7.2);
        else if (starCntCond(19, 20))   setMargin(-7, -9.5);
    } else if (groupCnt === 11 || groupCnt === 12) {
        if      (starCntCond(5, 8))     setMargin(-8, -8.8);
        else if (starCntCond(9, 12))    setMargin(-7, 3.2);
        else if (starCntCond(13, 16))   setMargin(-6.6, 9.2);
        else if (starCntCond(17, 20))   setMargin(-6.6, 2.6);
    } else if (groupCnt === 13 || groupCnt === 14 || groupCnt === 15 || groupCnt === 16) {
        if      (starCntCond(5, 8))     setMargin(-5, -6);
        else if (starCntCond(9, 12))    setMargin(-7, -6);
        else if (starCntCond(13, 16))   setMargin(-6.6, 0);
        else if (starCntCond(17, 20))   setMargin(-6.6, -4.9);
    } else if (groupCnt === 17 || groupCnt === 18 || groupCnt === 19 || groupCnt === 20) {
        if      (starCntCond(5, 8))     setMargin(-9, -5.5);
        else if (starCntCond(9, 12))    setMargin(-5, -0.5);
        else if (starCntCond(13, 16))   setMargin(-10, 0);
        else if (starCntCond(17, 20))   setMargin(-4, -2.5);
    }
};

let addingStarClassBySize = (object) => {
    let string = `${object.groupCtn},${object.starCtn}`;

    if (starScaleSet.get(9).has(string)) {
        // object.starWidth = object.stdStarWidth * .9;
        return ' nine';
    } else if (starScaleSet.get(8).has(string)) {
        // object.starWidth = object.stdStarWidth * .8;
        return ' eight';
    } else if (starScaleSet.get(7).has(string)) {
        // object.starWidth = object.stdStarWidth * .7;
        return ' seven';
    } else if (starScaleSet.get(6).has(string)) {
        // object.starWidth = object.stdStarWidth * .6;
        return ' six';
    } else if (starScaleSet.get(5).has(string)) {
        // object.starWidth = object.stdStarWidth * .5;
        return ' five';
    } else if (starScaleSet.get(4).has(string)) {
        // object.starWidth = object.stdStarWidth * .4;
        return ' four';
    } else if (starScaleSet.get(3).has(string)) {
        // object.starWidth = object.stdStarWidth * .3;
        return ' three';
    } else {
        // object.starWidth = object.stdStarWidth;
        return '';
    }
}

export default {
    setLastGroupMarginRight,
    setGroupSize,
    setGroupPadding,
    setStarPosition,
    addingStarClassBySize
};