let $ks = {
    //////////////////////
    // DOM 제어 및 판단 //
    //////////////////////

    // create element
    ce(opts) { // CE({ tag: "style", id: "thisId" class: "hey", targetElement: QS("head"), attribute: { type: "text/css" } })
        let element;
        element = document.createElement(opts.tag);
        if (opts.id) element.setAttribute('id', opts.id);
        if (opts.class) {
            if (opts.class.indexOf(' ') > -1) {
                let classNameArray = opts.class.split(' ');
                classNameArray.forEach((className) => { element.classList.add(className); });
            } else {
                element.classList.add(opts.class);
            }

        }
        if (opts.targetElement) opts.targetElement.appendChild(element);
        if (opts.targetElement && opts.insertBeforeRefEl) opts.targetElement.insertBefore(element, opts.insertBeforeRefEl);
        if (opts.attribute) {
            for (var attrName in opts.attribute) {
                element.setAttribute(attrName, opts.attribute[attrName]);
            }
        }
        return element;
    },

    // create svg element
    ceSvg(tag, targetElement) {
        let svgElement = document.createElementNS('http://www.w3.org/2000/svg', tag);
        targetElement.appendChild(svgElement);
        return svgElement;
    },

    // remove element
    removeElement(element) { if (element) element.parentNode.removeChild(element); },

    // getElementById
    getById(id, parentElement = document) { return parentElement.getElementById(id); },

    // getElementsByTagName
    getByTag(tagName, parentElement = document, index) {
        let result;
        result = Array.from(parentElement.getElementsByTagName(tagName));
        result = (index || index === 0) ? result[index] : result;
        return result;
    },

    // getElementsByClassName
    getByClass(className, parentElement = document, index) {
        let result;
        result = Array.from(parentElement.getElementsByClassName(className));
        result = (index || index === 0) ? result[index] : result;
        return result;
    },

    // querySelector
    qs(query, parentElement = document) { return parentElement.querySelector(query); },

    // querySelectorAll
    qsall(query, parentElement = document) { return Array.from(parentElement.querySelectorAll(query)); },

    // class 제어
    addClass (element, className) { className = className.split(' '); for (var i = 0; i < className.length; i++) element.classList.add(className[i]); },
    removeClass (element, className) { className = className.split(' '); for (var i = 0; i < className.length; i++) element.classList.remove(className[i]); },
    hasClass (element, className) { return element.classList.contains(className); },

    getRandomNumber(max, min = 0) { return Math.floor(Math.random() * max) + min },

    addHoverEvents(target, options) {
        let addEvent = (eType, classType) => {
            target.addEventListener(eType, (e) => {
                if (options) {
                    if (options.preventDefault) e.preventDefault();
                    if (options.additionalFn) options.additionalFn(e);
                }
                if (classType === 'add') this.addClass(target, 'hover');
                else this.removeClass(target, 'hover');
            }, false);
        };
        addEvent('mouseenter', 'add');
        addEvent('mouseleave', 'remove');
    },

    addDownEvents(target, options) {
        let addEvent = (eType, classType) => {
            target.addEventListener(eType, (e) => {
                if (options) {
                    if (options.preventDefault) e.preventDefault();
                    if (options.additionalFn) options.additionalFn(e, classType);
                }
                if (classType === 'add') this.addClass(target, 'clicked');
                else this.removeClass(target, 'clicked');
            }, false);
        };
        addEvent('mousedown', 'add');
        addEvent('touchstart', 'add');
        addEvent('mouseleave', 'remove');
        addEvent('touchcancel', 'remove');
        addEvent('mouseup', 'remove');
        addEvent('touchend', 'remove');
    },

    addFocusEvents(target, options) {
        let addEvent = (eType, classType) => {
            target.addEventListener(eType, (e) => {
                if (options) {
                    if (options.preventDefault) e.preventDefault();
                }
                if (classType === 'add') this.addClass(target, 'focused');
                else this.removeClass(target, 'focused');
            }, false);
        };
        addEvent('focus', 'add');
        addEvent('blur', 'remove');
    },

    summingNumberArray(array) { return array.reduce((accumulator, currentValue) => accumulator + currentValue); },

    getSize (target, size) {
        var value;
        switch (size) {
            case 'width':  value = target.getBoundingClientRect().width; break;
            case 'height': value = target.getBoundingClientRect().height; break;
            case 'top':    value = target.getBoundingClientRect().top; break;
            case 'left':   value = target.getBoundingClientRect().left; break;
            case 'right':  value = target.getBoundingClientRect().right; break;
            case 'bottom': value = target.getBoundingClientRect().bottom; break;
        }
        return value;
    },

    XYtransforming(target, x, y) {
        if (!target) return;
        target.style.transform = `translate(${x}px, ${y}px)`;
        target.style.msTransform = `translate(${x}px, ${y}px)`;
        target.style.webkitTransform = `translate(${x}px, ${y}px)`;
    },

    loadScriptFile(cssSrc, callBack) {
        var script = document.createElement('script');
        script.src = cssSrc;

        if (callBack) {
            script.onload = function () {
                callBack();
            };
        }
        document.body.appendChild(script);
    },

    loadCssFile(cssSrc, callBack) {
        var css = document.createElement("link");
        css.setAttribute("rel", "stylesheet");
        css.setAttribute("type", "text/css");
        css.setAttribute("href", cssSrc);

        document.head.appendChild(css);
    },

    timeNow() { return new Date().getTime(); },

    bodyLog(msg, opts) {
        var logbox = document.getElementById('bodyLogBox') ? document.getElementById('bodyLogBox') : this.ce({ tag: 'div', id: 'bodyLogBox', targetElement: document.body });
        logbox.style.position = 'fixed';
        logbox.style.right = 0;
        logbox.style.bottom = 0;

        if (opts && opts.clear) {
            logbox.innerHTML = msg;
        } else {
            logbox.innerHTML += '<br>';
            logbox.innerHTML += msg;
        }
    }
};

export default $ks;

