// class 제어 constructor
function $ClassControl(element){
    element.addClass = function (className) { className = className.split(' '); for (var i = 0; i < className.length; i++) element.classList.add(className[i]); }
    element.removeClass = function (className) { className = className.split(' '); for (var i = 0; i < className.length; i++) element.classList.remove(className[i]); }
    element.hasClass = function (className) { return element.classList.contains(className); }
}

// document 또는 parent에 query에 해당하는 element가 있는가?
function isThere (query, parent = document) { return parent.querySelectorAll(query).length !== 0; }

// object가 빈 값인가?
function isEmpty (object) { return object === undefined || object === null || object.length === 0 || Number.isNaN(object); }

// style tag에 css style 추가
function setStyles(JSON) {
    var styleTag, selectors, propertyPairs, allCssTexts;
    styleTag = this.getByTag('style') || this.ce({ tag: "style", targetElement: document.head, attribute: { type: "text/css" } });
    selectors = Object.keys(JSON);
    propertyPairs = this.getValues(JSON);
    allCssTexts = '';

    var selectorIndex, selectorCssText, names, values, nameIndex;
    for (selectorIndex = 0; selectorIndex < selectors.length; selectorIndex++) {
        selectorCssText = selectors[selectorIndex] + ' {';
        names = Object.keys(propertyPairs[selectorIndex]);
        values = this.getValues(propertyPairs[selectorIndex]);

        for (nameIndex = 0; nameIndex < names.length; nameIndex++) {
            selectorCssText += names[nameIndex] + ':' + values[nameIndex] + ';';
        }
        selectorCssText += '}';
        allCssTexts += selectorCssText;
    }
    styleTag.innerHTML = allCssTexts + styleTag.innerHTML;
}

