//////////////////
// JS animation //
//////////////////

function animate(opts) {
    var start = new Date,

        id = setInterval(function() {
            var timePassed = new Date - start,
                progress = timePassed / opts.duration;

            if (progress > 1)
                progress = 1;

            var delta = opts.delta(progress);
            opts.step(delta);

            if (progress === 1) {
                clearInterval(id)
            }

        }, opts.delay);

    GameManager.aniId = id;
}

function makeEaseInOut(delta) {
    return function(progress) {
        if (progress < .5) return delta(2 * progress) / 2;
        else return (2 - delta(2 * (1 - progress))) / 2;
    }
}

function makeEaseOut(delta) {
    return function(progress) {
        return 1 - delta(1 - progress);
    }
}

function elastic (progress) { return Math.pow(2,10 * (progress - 1)) * Math.cos(20 * Math.PI * 1.5 / 3 * progress); }
function linear (progress) { return progress; }
function quad (progress) { return Math.pow(progress, 2); }
function quint (progress) { return Math.pow(progress, 5); }
function circ (progress) { return 1- Math.sin(Math.acos(progress)); }
function back (progress) { return Math.pow(progress,2)*((1.5+ 1)*progress- 1.5); }
function bounce (progress) {
    for (var a = 0, b = 1; 1; a += b, b /= 2) {
        if (progress >= (7 - 4 * a) / 11) {
            return -Math.pow((11 - 6 * a - 11 * progress) / 4, 2) + Math.pow(b, 2);
        }
    }
}

function runAnimation (animateElement, stepAni, animateOption) {
    animate({
        delay : animateOption.delay,
        duration : animateOption.duration,
        delta :animateOption.delta,
        step : function(delta) {
            stepAni(animateElement, delta);
        }
    });
}





// effectSound
let efSound = (src) => {
    var efAudio = new Audio;
    var efPlay = function () {
        efAudio.removeEventListener('loadeddata', efPlay);
        efAudio.play();
    };
    efAudio.src = src;
    efAudio.addEventListener('loadeddata', efPlay);
    efAudio.load();
}

///////////
// EVENT //
///////////

var eventCheck = 'ontouchstart' in window || window.DocumentTouch && document instanceof DocumentTouch;

var GameManager = {
    event: {
        isTouchDevice: 'ontouchstart' in window || window.DocumentTouch && document instanceof DocumentTouch,
        eventSelector: function (eventType) {
            // console.log('□ this.isTouchDevice :', this.isTouchDevice);
            var selectedEvent;
            switch (eventType) {
                case 'eventDown':
                    selectedEvent = this.isTouchDevice ? 'touchstart' : 'mousedown';
                    break;
                case 'eventMove':
                    selectedEvent = this.isTouchDevice ? 'touchmove' : 'mousemove';
                    break;
                case 'eventUp':
                    selectedEvent = this.isTouchDevice ? 'touchend' : 'mouseup';
                    break;
                case 'eventOut':
                    selectedEvent = this.isTouchDevice ? 'touchleave' : 'mouseout';
                    break;
            }
            return selectedEvent;
        }
    }
};

function eventSelector (eventType, e) {
    var eventMaster;

    if (eventType === 'eventDown') {
        switch (GameManager.event.eventSelector('eventDown')) {
            case "mousedown":
                eventMaster = e;
                break;
            case "touchstart":
                e.preventDefault();
                eventMaster = e.touches.item(0);
                break;
        }
    } else if (eventType === 'eventMove') {
        switch (GameManager.event.eventSelector('eventMove')) {
            case "mousemove":
                eventMaster = e;
                break;
            case "touchmove":
                eventMaster = e.touches.item(0);
                break;
        }
    } else if (eventType === 'eventUp') {
        switch (GameManager.event.eventSelector('eventUp')) {
            case "mouseup":
                eventMaster = e;
                break;
            case "touchend":
                eventMaster = e.changedTouches[0];
                break;
        }
    } else if (eventType === 'eventOut') {
        switch (GameManager.event.eventSelector('eventOut')) {
            case "mouseout":
                eventMaster = e;
                break;
            case "touchleave":
                eventMaster = e.touches.item(0);
                break;
        }
    }
    return eventMaster;
};

function addEvent (target, eType, fnc) {
    var eventType;
    switch(eType){
        case 'mousedown': eventType = eventMaster.eventSelector('eventDown'); break;
        case 'mousemove': eventType = eventMaster.eventSelector('eventMove'); break;
        case 'mouseup':   eventType = eventMaster.eventSelector('eventUp'); break;
        case 'mouseout':  eventType = eventMaster.eventSelector('eventOut'); break;
        case 'click':  eventType = 'click'; break;
    }
    return target.addEventListener(eventType, fnc, false);
}
// removeEventListener
function removeEvent (target, eType, fnc) {
    var eventType;
    switch(eType){
        case 'mousedown': eventType = eventMaster.eventSelector('eventDown'); break;
        case 'mousemove': eventType = eventMaster.eventSelector('eventMove'); break;
        case 'mouseup':   eventType = eventMaster.eventSelector('eventUp'); break;
        case 'mouseout':  eventType = eventMaster.eventSelector('eventOut'); break;
        case 'click':  eventType = 'click'; break;
    }
    return target.removeEventListener(eventType, fnc, false);
}

// btn link (..ing)
function makeLinkBtns (btnData) { console.log('fn > makeLinkBtns'); console.log(btnData);
    var array = btnData instanceof Array ? btnData : [btnData];

    array.forEach(function(el){ new $LinkBtn(el); });
}

function $LinkBtn (object) { console.log('ctr > $LinkBtn'); console.log(object, this);
    this.btnClick = function(){
        window.open(object.linkUrl, '_self');
    }

    var eventType, fnc;
    eventType = (object.option && object.option.eventType) ? object.option.eventType : 'click';
    fnc = (object.option && object.option.fnc) ? object.option.fnc : this.btnClick;

    addEvent(object.btn, eventType, fnc);
}

