require('../less/groupSticker/master.less');

import $ks from './lib/ks';
import Responsive from './lib/responsive';
import Imgpreloader from './lib/imgPreloader';

import Popup from './groupSticker/popup';

import spriteImg from './lib/spriteImg';
import efSound from './lib/efSound';

import setPosAndSize from './groupSticker/setPosAndSize';

class GroupSticker {
    constructor(starCnt = 10, groupCnt = 10) {
        // 기본 설정
        this.starCtn = starCnt;
        this.groupCtn = groupCnt;
        this.clickedGroupIndex = 0;
        // this.stdStarWidth = 120;
        // this.starWidth;
        this.preloadedImgs = [];

        // 반응형
        this.responsives = [];
        this.responsives.push(new Responsive($ks.getById('wrap')));
        this.responsives.push(new Responsive($ks.getById('bgContainer')));

        // 메인 화면
        this.groupSticker = $ks.getById('groupSticker');
        this.groups = [];
        this.stars = [];

        // 팝업
        this.popup = new Popup(this);
    }

    // 기본 attribute 설정
    initialize() { console.log('method >> 1 groupSticker.initialize', $ks.timeNow());
        this.groupSticker.innerHTML = '';

        this.responsives.forEach((responsive) => { responsive.start(); });

        $ks.getById('wrap').style.opacity = 1;

        this.groups = [];
        this.stars = [];
        return this;
    }

    // 기본 화면에 그리기
    drawThings() { console.log('method >> 2 groupSticker.drawThings', $ks.timeNow());
        let group, setAll, span, ul, star,/* viewAniStar, hideAniStar, */ starAni, starClickArea;

        // 화면 상단 여백 설정
        if (this.groupCtn === 3)
            this.groupSticker.style.paddingTop = '70px';
        else if (this.groupCtn < 6)
            this.groupSticker.style.paddingTop = '40px';
        else
            this.groupSticker.style.paddingTop = '30px';

        let starSizeClassName = setPosAndSize.addingStarClassBySize(this); // 별 크기 설정을 위한 class 추가
        for (let i = 1, len1 = this.groupCtn; i <= len1; i++) {
            group = $ks.ce({
                tag: 'li',
                class: 'group',
                targetElement: this.groupSticker
            });

            setPosAndSize.setLastGroupMarginRight(group, i, this.groupCtn); // 필요시 마지막 group 오른쪽 여백 설정

            setAll = $ks.ce({
                tag: 'div',
                class: 'setAll',
                targetElement: group
            });
            setAll.groupIndex = i;
            this.setAllClickEv(setAll); // 번호 영역 클릭 이벤트(전체 보이기/숨기기)
            setPosAndSize.setGroupSize(group, setAll, this.groupCtn); // group 수에 따른 size 설정

            span = $ks.ce({
                tag: 'span',
                targetElement: setAll
            });
            span.innerHTML = i;

            ul = $ks.ce({
                tag: 'ul',
                targetElement: group
            });

            this.groups.push(ul);

            setPosAndSize.setGroupPadding(ul, this.groupCtn, this.starCtn); // group과 star 수에 따른 group padding 설정

            this.stars[i] = [];
            for (let j = 1, len2 = this.starCtn; j <= len2; j++) {
                star = $ks.ce({
                    tag: 'li',
                    class: `star${starSizeClassName}`,
                    targetElement: ul,
                });
                $ks.addClass(star, 'hide');
                this.controlStarClickEv(star); // 별 클릭 이벤트(보이기/숨기기)
                setPosAndSize.setStarPosition(star, this.groupCtn, this.starCtn);

                // viewAniStar = $ks.ce({
                //     tag: 'div',
                //     class: 'viewAni',
                //     targetElement: star
                // });
                //
                // hideAniStar = $ks.ce({
                //     tag: 'div',
                //     class: 'hideAni',
                //     targetElement: star
                // });

                starAni = $ks.ce({
                    tag: 'img',
                    class: 'starAni',
                    targetElement: star
                });
                starAni.src = './src/img/groupSticker/star_view_ani/01.png';

                starClickArea = $ks.ce({
                    tag: 'div',
                    class: 'clickArea',
                    targetElement: star
                });

                this.stars[i].push(star);
            }
        }

        return this;
    }

    // 번호 영역 클릭 이벤트(전체 보이기/숨기기)
    setAllClickEv(setAll) {
        setAll.addEventListener('click', (e) => {
            let target = e.target;
            while (!$ks.hasClass(target, 'setAll')) { target = target.parentElement; }

            this.clickedGroupIndex = target.groupIndex;

            if (!this.areAllStarsOnAni) { // ani 진행 중인 star가 있는가?
                efSound('./src/snd/groupSticker/allstar_ef.mp3');

                if (!this.areAllStarsOnView) { // 모든 star가 view 상태인가?
                    this.clickedStars.forEach((star) => {
                        if ($ks.hasClass(star, 'hide'))
                            this.showStarAni(star, true);
                    });
                } else {
                    this.clickedStars.forEach((star) => {
                        this.hideStarAni(star, true);
                    });
                }
            }
        }, false);

        return this;
    }

    // 별 클릭 이벤트(보이기/숨기기)
    controlStarClickEv(star) {
        star.addEventListener('click', (e) => {
            if ($ks.hasClass(e.target, 'clickArea')) {

                if ($ks.hasClass(star, 'hide')) {
                    this.showStarAni(star);
                } else if ($ks.hasClass(star, 'view')){
                    this.hideStarAni(star);
                }
            }
        }, false);

        return this;
    }

    // 별 나타나는 애니
    showStarAni(star, isAll = false) {
        let options = new Map();
        options.set('target', star);
        options.set('cnt', 16);
        options.set('intervalTime', 30);
        options.set('startFn', (target) => {
            if (!isAll) efSound('./src/snd/groupSticker/star_ef.mp3');

            $ks.removeClass(star, 'hide');
            $ks.addClass(star, 'viewing');
        });
        options.set('intervalFn', (target, cnt) => {
            let imgName = cnt < 9 ? `./src/img/groupSticker/star_view_ani/0${cnt + 1}.png` : `./src/img/groupSticker/star_view_ani/${cnt + 1}.png`;
            target.children[0].src = imgName;
        });
        options.set('endFc', (target) => {
            $ks.removeClass(star, 'viewing');
            $ks.addClass(star, 'view');
        });
        spriteImg(options);

        return this;
    }

    // 별 사라지는 애니
    hideStarAni(star, isAll = false) {
        let options = new Map();
        options.set('target', star);
        options.set('cnt', 13);
        options.set('intervalTime', 30);
        options.set('startFn', (target) => {
            if (!isAll) efSound('./src/snd/groupSticker/star_ef.mp3');
            $ks.removeClass(star, 'view');
            $ks.addClass(star, 'hiding');
        });
        options.set('intervalFn', (target, cnt) => {
            let imgName = cnt < 9 ? `./src/img/groupSticker/star_hide_ani/0${cnt + 1}.png` : `./src/img/groupSticker/star_hide_ani/${cnt + 1}.png`;
            target.children[0].src = imgName;
        });
        options.set('endFc', (target) => {
            window.setTimeout(() => { target.children[0].src = './src/img/groupSticker/star_view_ani/01.png'; }, 0);
            $ks.removeClass(star, 'hiding');
            $ks.addClass(star, 'hide');
        });
        spriteImg(options);

        return this;
    }

    // 모둠 설정 버튼 초기화
    initSettingBtn() { console.log('method >> 3 groupSticker.initSettingBtn', $ks.timeNow());
        let btn = $ks.getById('groupSettingBtn');
        $ks.addHoverEvents(btn);
        $ks.addDownEvents(btn);
        btn.addEventListener('click', () => {
            this.popup.togglePopup('open');
        }, false);

        return this;
    }

    // getter
    get clickedStars() { return this.stars[this.clickedGroupIndex] }
    get areAllStarsOnAni() { return this.clickedStars.some((star) => { return $ks.hasClass(star, 'viewing') || $ks.hasClass(star, 'hiding') }); }
    get areAllStarsOnView() { return this.clickedStars.every((star) => { return $ks.hasClass(star, 'view'); }); }
}

const groupSticker = new GroupSticker(10, 10);
const imgPreloader = new Imgpreloader(groupSticker.preloadedImgs);

groupSticker.initialize();
let imgPreloaderOptArray = [
    {
        array: false,
        path: './src/img/groupSticker/star_view_ani/',
        ext: 'png',
        count: 16,
        startIndex: 1
    },
    {
        array: false,
        path: './src/img/groupSticker/star_hide_ani/',
        ext: 'png',
        count: 13,
        startIndex: 1
    },
    {
        array: false,
        path: './src/img/groupSticker/group/group_bg_',
        ext: 'png',
        count: 8,
        startIndex: 1
    }
];
imgPreloader.preload(imgPreloaderOptArray[0])
.then(imgPreloader.preload(imgPreloaderOptArray[1]))
.then(imgPreloader.preload(imgPreloaderOptArray[2]))
.then(() => {
    groupSticker.drawThings().initSettingBtn().popup.initialize();
});
