let spriteImg;
spriteImg = (opts) => {
    if (opts.has('startFn')) opts.get('startFn')(opts.get('target'));

    if (opts.has('cnt') && opts.has('intervalTime') && opts.has('intervalFn')) {
        let cnt = 0;
        opts.get('target').intervalID = setInterval(() => {
            if (cnt === opts.get('cnt') - 1) {
                clearInterval(opts.get('target').intervalID);
                opts.get('endFc')(opts.get('target'));
            }
            opts.get('intervalFn')(opts.get('target'), cnt);
            cnt++;
        }, opts.get('intervalTime'));
    }
}
export default spriteImg;