import $ks from '../lib/ks';
import setNumOnlyInput from '../lib/setNumOnlyInput';

class Popup {
    constructor(groupSticker) {
        this.groupSticker = groupSticker;

        this.popupElement = $ks.getById('popup');;
        this.arrows = [];
        this.groupInput;
        this.starInput;
        this.popupOkBtn;

        this.alertElement = $ks.getById('alert');
        this.alertOKBtn;
    }

    // 팝업 초기 설정
    initialize() { console.log('method >> 4 popup.initialize', $ks.timeNow());
        this.drawPopup().addPopupEvents();
        this.drawAlert().addAlertEvents();

        // input 글자 수 제한
        setNumOnlyInput(this.groupInput, 2);
        setNumOnlyInput(this.starInput, 2);
    }

    // popup 요소 그리기
    drawPopup() { console.log('method >> 5 popup.drawPopup', $ks.timeNow());
        let popupInner, groupSelectBox, starSelectBox;

        popupInner = $ks.ce({
            tag: 'div',
            class: 'popupInner',
            targetElement: this.popupElement
        });

        // 모둠 선택 영역
        $ks.ce({
            tag: 'p',
            targetElement: popupInner
        }).textContent = '모둠 수를 선택해 주세요.';

        groupSelectBox = $ks.ce({
            tag: 'div',
            class: 'inputBox',
            targetElement: popupInner
        });
        groupSelectBox.id = 'groupSelectBox';

        this.groupInput = $ks.ce({
            tag: 'input',
            targetElement: groupSelectBox
        });
        this.groupInput.setAttribute('type', 'text');

        this.drawArrows(groupSelectBox);

        // 스티커 선택 영역
        $ks.ce({
            tag: 'p',
            targetElement: popupInner
        }).textContent = '스티커 개수를 선택해 주세요.';

        starSelectBox = $ks.ce({
            tag: 'div',
            class: 'inputBox',
            targetElement: popupInner
        });
        starSelectBox.id = 'starSelectBox';

        this.starInput = $ks.ce({
            tag: 'input',
            targetElement: starSelectBox
        });
        this.starInput.setAttribute('type', 'text');

        this.drawArrows(starSelectBox);

        this.popupOkBtn = $ks.ce({
            tag: 'div',
            class: 'okBtn',
            targetElement: popupInner
        });
        this.popupOkBtn.textContent = 'OK';

        return this;
    }

    // popup 화살표 그리기
    drawArrows(parentElement) {
        let arrows, upper, lower;
        arrows = $ks.ce({
            tag: 'div',
            class: 'arrows',
            targetElement: parentElement
        });
        upper = $ks.ce({
            tag: 'div',
            class: 'arrow upper',
            targetElement: arrows
        });
        lower = $ks.ce({
            tag: 'div',
            class: 'arrow lower',
            targetElement: arrows
        });

        this.arrows.push(upper);
        this.arrows.push(lower);
    }

    // popup에 event 걸기
    addPopupEvents() { console.log('method >> 6 popup.addPopupEvents', $ks.timeNow());
        // OK btn
        $ks.addHoverEvents(this.popupOkBtn);
        $ks.addDownEvents(this.popupOkBtn);
        this.popupOkBtn.addEventListener('click', (e) => {
            this.popupOkBtnClickFn(e, this);
        }, false);

        // 화살표
        this.arrows.forEach((arrow) => {
            $ks.addHoverEvents(arrow);
            $ks.addDownEvents(arrow);
            arrow.addEventListener('click', (e) => {
                this.arrowClickEv(e, this);
            }, false);
        });

        return this.initInputValues();
    }

    // popup OK 버튼 클릭 이벤트 제어
    popupOkBtnClickFn(e, popupObj) {
        if (this.valuesAreCorrect) {
            this.groupSticker.groupCtn = this.values.group;
            this.groupSticker.starCtn = this.values.star;
            popupObj.togglePopup('close');
            popupObj.groupSticker.initialize().drawThings();
        } else {
            popupObj.toggleAlert('open');
        }
    }

    // 팝업 input arrow click event 제어
    arrowClickEv(e, popupObj) {
        let isGroup, isUpper;
        isGroup = e.target.parentNode.parentNode.id === 'groupSelectBox'; // group인가 star인가?
        isUpper = $ks.hasClass(e.target, 'upper'); // upper인가 lower인가?

        if (isGroup) {
            if (isUpper) popupObj.sumMinInput(popupObj.groupInput, 'sum', isGroup);
            else popupObj.sumMinInput(popupObj.groupInput, 'min', isGroup);
        } else {
            if (isUpper) popupObj.sumMinInput(popupObj.starInput, 'sum', isGroup);
            else popupObj.sumMinInput(popupObj.starInput, 'min', isGroup);
        }
    }

    // arrow click시 더하기 / 빼기 동작
    sumMinInput(input, sumOrMin, isGroup) {
        if (input.value === '') {
            if (isGroup) input.value = 3;
            else input.value = 5;
        }
        let numberedVal = Number.parseInt(input.value);
        if (sumOrMin === 'sum') {
            if (numberedVal >= 20) input.value = 20;
            else input.value = numberedVal + 1;
        }
        else if (sumOrMin === 'min') {
            if (isGroup) {
                if (numberedVal <= 3) input.value = 3;
                else input.value = numberedVal - 1;
            }
            if (!isGroup) {
                if (numberedVal <= 5) input.value = 5;
                else input.value = numberedVal - 1;
            }
        }
    }

    // toggle Popup
    togglePopup(openClose) {
        this.initInputValues();

        if (openClose === 'open')
            this.popupElement.style.display = 'block';
        else
            this.popupElement.style.display = 'none';
    }

    // popup input value 초기화하기
    initInputValues() {
        this.groupInput.value = this.groupSticker.groupCtn;
        this.starInput.value = this.groupSticker.starCtn;
    }

    drawAlert() { console.log('method >> 7 popup.drawAlert', $ks.timeNow());
        $ks.ce({
            tag: 'div',
            class: 'title',
            targetElement: this.alertElement
        }).textContent = '안내';

        $ks.ce({
            tag: 'p',
            targetElement: this.alertElement
        }).innerHTML = "모둠 수는 3에서 20 사이의 숫자를,<br>스티커 개수는 5에서 20 사이의 숫자를 입력해주세요.";

        this.alertOKBtn = $ks.ce({
            tag: 'div',
            class: 'okBtn',
            targetElement: this.alertElement
        });
        this.alertOKBtn.textContent = 'OK';

        return this;
    }

    addAlertEvents() { console.log('method >> 8 popup.addAlertEvents', $ks.timeNow());
        $ks.addHoverEvents(this.alertOKBtn);
        $ks.addDownEvents(this.alertOKBtn);
        this.alertOKBtn.addEventListener('click', () => {
            this.toggleAlert('close');
        }, false);
    }

    toggleAlert(openClose) {
        if (openClose === 'open') {
            this.alertElement.style.display = 'block';
        } else {
            this.alertElement.style.display = 'none';
            this.initInputValues();
        }
    }

    // getter
    get values() {
        return {
            group: Number.parseInt(this.groupInput.value),
            star: Number.parseInt(this.starInput.value)
        };
    }
    get valuesAreCorrect() { return (this.values.group >= 3 && this.values.group <= 20) && (this.values.star >= 5 && this.values.star <= 20); }
}

export default Popup;
